import React from "react";
import Popup from "reactjs-popup";
import TimeField from 'react-simple-timefield';
import './App.css';
import Dropdown from 'react-dropdown'
import 'react-dropdown/style.css'
import YouTube from 'react-youtube';
import axios from 'axios';

export default class UsernameModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: ''
        };
        
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
      }

      
      handleChange(event) {
        this.setState({username: event.target.value});
      }
    
      handleSubmit(event) {
        event.preventDefault();
        const {username} = this.state;
        const {sessionID, closeUsernameModal} = this.props;
        if(!username){  
            event.preventDefault();
            alert('Please fill out the information');
        }
        else{
            //TODO axios post to the backend
            axios.put('https://rubyslippers.kixlab.org/backend/sessions/'+ sessionID +'/add_username/', {
                username: username,
            });
            closeUsernameModal();
        }
        return false;
      }

      


    render() {
        const {open, closeUsernameModal} = this.props;
        if(open){
            return (
                <Popup open = {open}  closeOnDocumentClick= {false}>
                    {close => (
                    <div className="modal">
                        <a className="close" onClick={() => {closeUsernameModal()}}>
                        &times;
                        </a>
                        <div className="header"> What is your name?</div>
                        <div className="content">
                        {" "}
                        (Please answer to receive compensation)
                        </div>
                        <div className="actions">
                        <form onSubmit={this.handleSubmit}>
                            {/* <div className="form"> */}
                            <input type="text" value={this.state.username} onChange={this.handleChange} /> 
            
                            <button className="button" onClick={(event) => {this.handleSubmit(event, closeUsernameModal); return false;}}>
                                Submit
                            </button>
                            {/* </div> */}
                        </form>
                        </div>
                    </div>)}
                </Popup>
            )
        }
        else{
            return (null);
        }
        }};